
import ViewBox from 'vux/src/components/view-box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
props: {
placeholder: String,
homeUrl: String,
detailUrl: String,
buildDetailUrl: {
type: Function,
default () {
return null
}
},
value: {
type: Array,
default () {
return []
}
}
},
components: {
ViewBox,
XInput,
Group
},
data(){
return {
input:'',




showTime:false
}
},
watch:{
value:function(a){

this.$nextTick(function(){
this.$refs.vbox.scrollTo(99999999)
})
}
},
methods:{
putMsg(msg){
if(!msg) return
if(!msg.content) return
msg.type = msg.type !=1 ? 0 : 1
msg.time = msg.time || this.getTime()



this.value.push(msg)
},
getDetailUrl(val){
var url = ''
if(this.buildDetailUrl)
url = this.buildDetailUrl(val)
if(url)
return url
return this.detailUrl+'/'+val.id
},
getTime(){
var date = new Date();
return date.getFullYear() + "-"
+ (date.getMonth()+1) + "-"
+ date.getDate() + " "
+ date.getHours() + ":"
+ date.getMinutes() + ":"
+ date.getSeconds()
},
handleSend(){
console.warn(this.input)
this.input = this.input.trim()
if(this.input=='')
return this.focus();
this.putMsg({content:this.input ,type:0})
this.$emit('on-send',this.input)
this.input=''
this.focus()
},
focus(){
this.$refs.txt.focus()
},
handleHome(){
console.log(this.homeUrl)
this.$router.push(this.homeUrl||'/')
},
isArray(arr){
return Object.prototype.toString.call(arr)=='[object Array]'
}
},
mounted(){
console.log(this.value)
this.$nextTick(function(){
this.focus()
})
}
}



