import MChat from '@/components/common/MChat'

export default {
  components:{
      MChat
  },
  data(){
      return {
          message:[
              {type:1,content:'您好，请问有什么需要帮助的吗'}
             // {type:1,content:[{id:'aaa',text:'Top 文章1'},{id:'bbb',text:'Top 文章2'}]}
          ],
          isPushHot:false
      }
  },
  methods:{
    onSend(msg){
        console.log('onSend',msg)
        var self = this
        setTimeout(function(){
                // self.$refs.mchat.putMsg({type:1,content:'您发送了 '+msg})
                self.message.push({type:1,content:[{text:msg,id:'aaa'},{text:msg,id:'bbb'}]})
        },500)
        // this.getData(msg).then((arr)=>{
        //     this.message.push({type:1,content:arr || '没有找到相关信息，请换一个关键字查询吧~'})
        // })
      },
      bDetail(d){
          return {path:'/Product_Form/'+d.id+'?type='+d.type};
      },
      getData(kw){
        return new Promise((resolve,reject)=>{
            zskQuery({
                "token":"",
                "empid": "1111111", 
                "serviceName": "pcrm", 
                "func": "sp_CzApp_GetKeyWords", 
                "params": JSON.stringify(kw?{type:"keywordschat" ,words:kw}:{type:"hot"}), 
                "ext": "procedure" 
            },data =>{
                console.log(data)
                resolve(data)
            });
        })
      },
      pushHot(){
        if(this.isPushHot)
          return
        this.isPushHot = true
        this.getData().then((arr)=>{
            if(arr)
                this.message.push({type:1,content:arr})
        })
      }
  },
  mounted(){
    var kw= this.$route.query.kw
    if(!kw)
        this.pushHot()
  },
   beforeRouteEnter(to, from, next) {
    console.log(to,from)
    next(vm => {
        var kw= vm.$route.query.kw
        if(kw){
            vm.message.push({content:kw ,type:0})
            return  vm.onSend(kw)
        }
        vm.pushHot()
    });
  }
}



